<template>
  <a-spin :spinning="spinning" class="class_manage">
    <div class="nav">
      <a-breadcrumb>
        <a-breadcrumb-item><a href="/admin/home">首页</a></a-breadcrumb-item>
        <a-breadcrumb-item><a href="/admin/book/manage">图书管理</a></a-breadcrumb-item>
        <a-breadcrumb-item>分类管理</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <div>
      <div class="margin_bottom_20">
        <span>分类名称：</span>
        <a-select
          :options="firstCateOptions"
          v-model="searchParams.first_category_id"
          placeholder="一级分类"
        ></a-select>
        <a-select
          :options="secondCateOptions"
          v-model="searchParams.second_category_id"
          placeholder="二级分类"
        ></a-select>
        <a-button type="primary" @click="search" style="margin-right: 12px;">搜索</a-button>
        <a-button @click="reset">重置</a-button>
      </div>
      <div class="margin_bottom_20">
        <a-button type="primary" style="margin-right: 20px;" @click="createClass">创建分类</a-button>
        <a-button type="primary" @click="openImport">导入分类</a-button>
      </div>

      <s-table
        ref="table"
        bordered
        size="default"
        row-key="id"
        :columns="columns"
        :data="loadData"
      >
        <template slot="first_cate" slot-scope="text, record">
          {{record.firstCate && record.firstCate.category_name}}
        </template>
        <template slot="action" slot-scope="text">
          <a-button type="link" @click="editClass(text.id)">编辑</a-button>
          <a-button type="link" @click="deleteClass(text.id)">删除</a-button>
        </template>
      </s-table>

    </div>
    <!--  -->
    <a-modal
      v-model="createVisible"
      title="创建分类"
      @ok="handleCreate"
    >
      <a-form>
        <a-form-item label="一级分类">
          <a-input v-model="params.first_cate"></a-input>
        </a-form-item>
        <a-form-item label="二级分类">
          <a-input v-model="params.second_cate"></a-input>
        </a-form-item>
      </a-form>
    </a-modal>
    <a-modal
      v-model="editVisible"
      title="编辑分类"
      @ok="handleEidt"
    >
      <a-form>
        <a-form-item label="一级分类">
          <a-input v-model="editParams.first_cate"></a-input>
        </a-form-item>
        <a-form-item label="二级分类">
          <a-input v-model="editParams.second_cate"></a-input>
        </a-form-item>
      </a-form>
    </a-modal>

    <!--  -->
    <a-modal
      v-model="importVisible"
      title="导入分类"
      width='60%'
      :footer="null"
    >
      <a-button type="primary" style="margin-bottom: 12px;">
        <a :href="proxy + prefix + '/category/template'" download>模板下载</a>
      </a-button>
      <a-form
        layout="inline"
      >
        <a-form-item label="请选择文件上传">
          <a-upload :file-list="fileList" :remove="handleRemove" :before-upload="beforeUpload">
            <a-button v-if="fileList.length === 0"> <a-icon type="upload" /> 选择文件 </a-button>
          </a-upload>
        </a-form-item>
        <a-form-item>
          <a-button type="primary" @click="uploadFile">上传</a-button>
        </a-form-item>
      </a-form>
      <h3 style="padding: 12px 0;">错误信息</h3>
      <div>导入成功{{categoryResult.successNum || 0}}条数据，导入失败{{categoryResult.errNum || 0}}条数据，请修改后再次导入！</div>
      <s-table
        ref="resultTable"
        bordered
        size="default"
        :row-key="record => record.errMsg + Math.random()"
        :columns="resultColumn"
        :data="resultData"
      ></s-table>
    </a-modal>
  </a-spin>
</template>
<script>
import { Select, Modal } from 'ant-design-vue'
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'ClassManage',
  components: { ASelect: Select, STable, AModal: Modal },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '一级分类',
        scopedSlots: { customRender: "first_cate"},
      },
      {
        title: '二级分类',
        dataIndex: 'category_name'
      },
      {
        title: '操作',
        width: '250px',
        scopedSlots: { customRender: "action"},
      },
    ]

    const resultColumn = [
      { 
        title: '一级分类',
        dataIndex: 'firstCate'
      },
      {
        title: '二级分类',
        dataIndex: 'secondCate'
      },
      {
        title: '错误信息',
        dataIndex: 'errMsg'
      }

    ]
    let params = {
      first_cate: undefined,
      second_cate: undefined
    }

    let searchParams = {
      first_category_id: undefined,
      second_category_id: undefined
    }

    let editParams = {
      first_cate: '',
      second_cate: ''
    }

    return {
      createVisible: false,
      editVisible: false,
      columns,
      resultColumn,
      params,
      editParams,
      searchParams,
      spinning: false,
      currentEditId: null,
      importVisible: false,
      fileList: [],
      proxy: process.env.VUE_APP_API_URL,
      prefix: process.env.VUE_APP_PREFIX,
      loadData: paramters => {
        return this.getCategoryList(Object.assign({}, paramters, this.searchParams))
      },
      resultData: paramters => {
        return new Promise((resolve) => {
          resolve({
            list: this.categoryResult.errorData || [],
            total: this.categoryResult.errNum || 0
          })
        })
      }
    }
  },
  computed: {
    ...mapState('booksManage', ['categoryList', 'allCategoryList', 'categoryDetail']),
    ...mapState('file', ['categoryResult']),
    secondCateOptions() {
      return this.allCategoryList && this.allCategoryList.second && this.allCategoryList.second.map(item => {
          return {
            label: item.category_name,
            value: item.id
          }
        })
    },
    firstCateOptions() {
      return this.allCategoryList && this.allCategoryList.first && this.allCategoryList.first.map(item => {
          return {
            label: item.category_name,
            value: item.id,
          }
        })
    }
  },
  watch: {
    importVisible() {
      if (!this.importVisible) {
        console.log('a')
        this.$store.commit('file/SET_CATEGORY_RESULT', {})
        this.$refs.resultTable.refresh(true)
        this.fileList = []
      }
    }
  },
  methods: {
    ...mapActions('booksManage', {
      getCategoryList: 'getBookCategory',
      getCategoryDetail: 'getCategoryDetail',
      addCategory: 'addCategory',
      updateCategory: 'updateCategory',
      deleteCategory: 'deleteCategory'
    }),
    ...mapActions('file', {
      categoryTemplate: 'categoryTemplate',
      categoryImport: 'categoryImport'
    }),
    createClass() {
      this.createVisible = true
    },
    handleCreate() {
      this.addCategory(this.params).then(res => {
        this.$message.success(res.message)
        this.createVisible = false
      })
    },
    handleEidt() {
      this.updateCategory({
        id: this.currentEditId,
        data: this.editParams
      }).then(res => {
        this.$message.success(res.message)
        this.editVisible = false
      })
    },
    editClass(id) {
      this.spinning = true
      this.currentEditId = id
      this.getCategoryDetail(id).then(res => {
        this.editParams.second_cate = res.category_name
        this.editParams.first_cate = res.firstCate.category_name
        this.editVisible = true
      }).finally(() => {
        this.spinning = false
      })
    },
    deleteClass(id){
      const _this = this
      this.$confirm({
        title: '删除分类',
        content: '你确定要删除些分类？',
        okText: '确认',
        cancelText: '取消',
        onOk() {
          _this.deleteCategory(id).then(res => {
            _this.$message.success('删除成功')
            _this.$refs.table.refresh(true)
          })
        },
      })
    },
    reset() {
      this.searchParams = {
        first_category_id: undefined,
        second_category_id: undefined
      }
      this.$refs.table.refresh(true)
    },
    search() {
      this.$refs.table.refresh(true)
    },
    handleRemove(file) {
      const index = this.fileList.indexOf(file);
      const newFileList = this.fileList.slice();
      newFileList.splice(index, 1);
      this.fileList = newFileList;
    },
    beforeUpload(file) {
      this.fileList = [...this.fileList, file];
      return false;
    },
    getTemplate() {
      this.categoryTemplate().then(res => {
        this.download(res)
      })
      
    },
    uploadFile() {
      if (this.fileList.length === 0) {
        this.$message.info('请先上传模板文件')
        return ;
      }
      let formdata = new FormData()
      formdata.append('file', this.fileList[0])
      this.categoryImport(formdata).then(res => {
        this.$message.info(res.message)
        this.$refs.resultTable.refresh(true)
        this.$refs.table.refresh(true)
      })
    },
    openImport() {
      this.importVisible = true
    }
  },
  created() {
    
  }
}
</script>
<style lang="scss" scoped>
.ant-select {
  width: 200px;
  margin-right: 10px;
}
.margin_bottom_20 {
  margin-bottom: 20px;
}
/deep/.ant-upload {
  width: 300px;
  button {
    width: 100%;
  }
}
/deep/.ant-upload-list-item {
  width: 300px;
}
</style>